export default {
	data() {
		return {
			news:[]
		};
	},
	//表示页面加载完毕
	onLoad() {
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news', //仅为示例，并非真实接口地址。
			data: {},
			header: {},
			success: res => {
				this.news = res.data;
			}
		});
	},
	methods: {
		openinfo(e){
			let newsid = e.currentTarget.dataset.newsid;
			//页面跳转
			uni.navigateTo({
				url: '../info/info?newsid='+newsid
			});
		}
	}
};
